import { Modal } from "react-bootstrap";
import React from "react";
import {
  Nav,
  Navbar,
  NavbarBrand,
  Button,
  Container,
  ModalTitle,
  Form,
} from "react-bootstrap";
import ModalHeader from "react-bootstrap/esm/ModalHeader";
import NavbarCollapse from "react-bootstrap/esm/NavbarCollapse";
import NavbarToggle from "react-bootstrap/esm/NavbarToggle";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { useState } from "react";

const Styles = styled.div`
  a,
  .navbar-brand,
  .navbar-nav,
  .navbar-link {
    color: #adb1b8;
    &:hover {
      color: white;
    }
  }
`;

export default function NaviBar() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Styles>
        <Navbar collapseOnSellect expand="lg" bg="dark" variant="dark">
          <Container>
            <NavbarBrand>Voronko Nails</NavbarBrand>
            <NavbarToggle aria-controls="responsive-navbar-nav" />
            <NavbarCollapse id="responsive-navbar-nav" />
            <Nav className="mr-auto">
              <Nav.Link>
                <Link to="/">Головна </Link>
              </Nav.Link>
              <Nav.Link>
                <Link to="/services">Послуги </Link>
              </Nav.Link>
              <Nav.Link>
                <Link to="/reviews">Відгуки </Link>
              </Nav.Link>
              <Nav.Link>
                <Link to="/contacts">Контакти </Link>
              </Nav.Link>{" "}
            </Nav>
            <Nav>
              <Button variant="primary" className="mr-6" onClick={handleShow}>
                Записатися на послуги
              </Button>
            </Nav>
          </Container>
        </Navbar>
      </Styles>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Залишити заявку</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form action="https://formspree.io/f/meqvakro" method="POST">
            <Form.Group controlId="fromBasicPhone">
              <Form.Label>Мобільний телефон</Form.Label>
              <Form.Control
                type="phone"
                placeholder="Введіть ваш телефон"
                name="phone"
                required
              />
              <Form.Text className="text-muted">
                Конфіденційно! Ніколи не буде наданий стороннім особам
              </Form.Text>
              <Form.Label>Бажана дата та час</Form.Label>
              <Form.Control
                type="text"
                placeholder="Введіть бажану дату та час"
                name="date"
                required
              />
            </Form.Group>

            <Form.Group controlId="fromBasicCheckbox">
              <Form.Check type="checkbox" label="Запам'ятати мене" />
            </Form.Group>
            <Modal.Footer>
              <Button variant="secondary" type="submit">
                Відправити
              </Button>
            </Modal.Footer>
          </Form>
        </Modal.Body>
      </Modal>
    </>
  );
}
