import React from 'react';
import { Jumbotron as Jumbo, Container } from 'react-bootstrap';
import figers from "../figers.jpg";
import styled from 'styled-components';

const Styles = styled.div`
  .jumbo {
    background: url(${figers}) no-repeat fixed bottom;
    background-size: cover;
    color: #efefef;
    height: 400px;
    position: relative;
    x-index: -2;
  }
  .overlay {
    background-color: #000;
    opacity: 0.6;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    x-index: -1;
  }
`;

const Jumbotron = () => (
  <Styles>
    <Jumbo fluid className="jumbo">
      <div className="overlay"></div>
      <Container>
        <h1>Voronko Nails</h1>
        <p>
          Тут можна багато чого написати і розказати. Головні техніки та
          правила, за якими працювати, матеріали та комплектуючі до
          інструментів, якими я користуюся Тут можна багато чого написати і
          розказати. Головні техніки та правила, за якими працювати, матеріали
          та комплектуючі до інструментів, якими я користуюся Тут можна багато
          чого написати і розказати. Головні техніки та правила, за якими
          працювати, матеріали та комплектуючі до інструментів, якими я
          користуюся
        </p>
      </Container>
    </Jumbo>
  </Styles>
);

export default Jumbotron;