import React from "react";
import { Container } from "react-bootstrap";
import instagram from "../img/instagram.png";
import facebook from "../img/facebook.png";

const Footer = () => (
  <Container
    fluid
    style={{
      backgroundColor: "#212529",
      color: "#fff",
    }}
  >
    <p style={{ display: "flex", justifyContent: "center", padding: "20px" }}>
      Слідкуй за мною:
      <a
        style={{ display: "flex", justifyContent: "center", padding: "10px" }}
        href="https://www.instagram.com/voronkotaniusha/"
        href="https://www.facebook.com/profile.php?id=100008077891031"
      >
        <img className="d-block w-10" src={instagram} alt="Instagram logo" />
        <img className="d-block w-10" src={facebook} alt="Facebook logo" />
      </a>
    </p>
    <Container
      style={{ display: "flex", justifyContent: "center", padding: "10px" }}
    >
      <p> Розробник сайту Voronko Oleksandr </p>
    </Container>
  </Container>
);

export default Footer;
