import { Carousel } from "react-bootstrap";
import React from "react";
import figers from "../figers.jpg";
import colors from "../colors.jpg";
import gold from "../gold.jpg";
import yellow from "../yellow.jpg";


export default function Slider() {
  return (
    <Carousel>
      <Carousel.Item style={{ height: "800px" }}>
        <img className="d-block w-100" src={yellow} alt="First slide" />
        <Carousel.Caption>
          <h2>VORONKO NAILS</h2>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item style={{ height: "800px" }}>
        <img className="d-block w-100" src={figers} alt="Second slide" />
        <Carousel.Caption>
          <h2>VORONKO NAILS</h2>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item style={{ height: "800px" }}>
        <img className="d-block w-100" src={colors} alt="Third slide" />
        <Carousel.Caption>
          <h2>VORONKO NAILS</h2>
        
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
  );
}
